'use client';

import * as React from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@/hooks/use-auth';
import LoginForm from '@/components/admin/login-form';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { Loader2 } from 'lucide-react';

export default function AdminLoginPage() {
  const { user, loading } = useUser();
  const router = useRouter();

  React.useEffect(() => {
    // Si el usuario ya está autenticado y no está cargando, lo redirige al panel.
    if (!loading && user) {
      router.push('/admin');
    }
  }, [user, loading, router]);

  // Muestra un estado de carga mientras se verifica la autenticación.
  if (loading || user) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  // Si no está autenticado, muestra el formulario de login.
  return (
    <div className="flex items-center justify-center min-h-[calc(100vh-8rem)]">
      <Card className="w-full max-w-md">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">Acceso del Personal</CardTitle>
          <CardDescription>Ingrese sus credenciales para acceder al panel.</CardDescription>
        </CardHeader>
        <CardContent>
          <LoginForm />
        </CardContent>
      </Card>
    </div>
  );
}
