'use client';
import React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useAuth } from '@/hooks/use-auth';
import { toast } from 'sonner';
import { Loader2, LogIn } from 'lucide-react';

const formSchema = z.object({
  email: z.string().email("Debe ser un correo electrónico válido."),
  password: z.string().min(6, "La contraseña debe tener al menos 6 caracteres."),
});

export default function LoginForm() {
  const { signIn } = useAuth();
  const [isLoading, setIsLoading] = React.useState(false);

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: { email: '', password: '' },
  });

  async function onSubmit(values) {
    setIsLoading(true);
    try {
      await signIn(values.email, values.password);
      // La redirección se maneja en la página `AdminLoginPage`
      toast.success('¡Inicio de sesión exitoso!');
    } catch (error) {
      console.error("Error de inicio de sesión:", error);
      toast.error(error.code === 'auth/invalid-credential' 
        ? "Credenciales incorrectas. Verifique su email y contraseña."
        : "Ocurrió un error inesperado. Inténtelo de nuevo."
      );
    }
    setIsLoading(false);
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        <FormField control={form.control} name="email" render={({ field }) => ( <FormItem><FormLabel>Correo Electrónico</FormLabel><FormControl><Input type="email" placeholder="su.correo@dirisls.gob.pe" {...field} /></FormControl><FormMessage /></FormItem> )} />
        <FormField control={form.control} name="password" render={({ field }) => ( <FormItem><FormLabel>Contraseña</FormLabel><FormControl><Input type="password" placeholder="********" {...field} /></FormControl><FormMessage /></FormItem> )} />
        <Button type="submit" disabled={isLoading} className="w-full">
          {isLoading ? (
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          ) : (
            <LogIn className="mr-2 h-4 w-4" />
          )}
          Ingresar
        </Button>
      </form>
    </Form>
  );
}
